import { Pipe, PipeTransform } from '@angular/core';

/**
 * Este pipe obtiene el valor de un string y lo corta dejando puntos supensivos cuando alcanza un tamao dado
 * ej hola si se realiza el truncate de 2 caracteres quedara ho...
 */
@Pipe({
    name: 'truncate'
})
export class TruncatePipe implements PipeTransform {
    /**
     * Funcin que retorna el texto cortado, agregando puntos suspensivos cuando supera el lmite
     * @param value string que se cortar
     * @param args puede obtener dos argumentos, uno es el tamao de caracteres
     *  y el otro opcional es el texto que se colocar luego de cortar el texto
     */
    transform(value: string, args: string[]): string {
        const limit = args.length > 0 ? parseInt(args[0], 10) : 20;
        const trail = args.length > 1 ? args[1] : '...';
        return value.length > limit ? value.substring(0, limit) + trail : value;
    }
}
